import React from 'react';
import ReactDOM from 'react-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import './index.css';
import { BrowserRouter, Route } from 'react-router-dom';
import { route } from './configurations';
import HomeScreen from './view/home-view';
import JujuDashboard from './view/juju/juju-dashboard-view';
import JujuSettings from './view/juju/juju-settings-view';


import imgDevice from './imgs/icons/icnDevice.png';
import imgJujuFernWhite from './imgs/juju/logoJujuFernWhite.png';
import imgProfile from './imgs/icons/icnProfile.png';

function profileClicked() {
    console.log('PROFILE CLICKED!');
}

ReactDOM.render(
    <BrowserRouter>
        <div className="text-center vh-100" style={{ display: 'flex', flex: 1, flexDirection: 'column', height: '100%', backgroundImage: `linear-gradient(#25272a, #181a1d)`, maxWidth: 500 }}>
            <div className="container-fluid" >
                <div className="row">
                    <div className="col-2" >&nbsp;</div>
                    <div className="col-8 text-center" >
                        <img className="img-fluid" src={imgJujuFernWhite} alt='' style={{ height: 50, width: 50, margin: 10 }} />
                    </div>
                    <div className="col-2 text-center" >
                        <img className="img-fluid" src={imgProfile} alt='' onClick={profileClicked} style={{ height: 40, width: 40, margin: 10, marginTop: 15 }} />
                    </div>
                </div>
            </div>

            <Route exact path={route.HOME} component={HomeScreen} />
            <Route path={route.JUJU_DASHBOARD} component={JujuDashboard} />
            <Route path={route.JUJU_SETTINGS} component={JujuSettings} />

            <div style={{ height: 80 }}>
                <img className="img-fluid" src={imgDevice} alt='' style={{ height: 50, width: 50, marginTop: 5 }} />
                <div style={{ color: '#ffff00', marginTop: -10, marginBottom: 10, fontSize: 14, fontFamily: 'Montserrat-Regular' }}>
                    DEVICE
                </div>
            </div>
        </div>
    </BrowserRouter>,
    document.getElementById('root')
);
