import topics, { bleObserver } from '../utilities/bleObserver';


const uuidServiceAdvertising = '0000180a-0000-1000-8000-00805f9b34fb';
const uuidServiceCommand = '01afc722-9944-ad4c-5951-d1cca699ba0f';

const uuidCharName = '00002a00-0000-1000-8000-00805f9b34fb';
//const uuidCharManufacturer = '00002a29-0000-1000-8000-00805f9b34fb';
const uuidCharFirmwareVersion = '00002a26-0000-1000-8000-00805f9b34fb';
const uuidCharCommand = 'acee8829-8791-42f0-51b2-26fa29603130';
const uuidCharResponse = '01cb8a25-2498-264b-2929-e20421ef53b2';


class BLEController {
    constructor() {
        this.isConnected = false;
        this.device = null;
        this.server = null;

        this.svcInfo = null;
        this.svcCommand = null;

        this.charCommand = null;
        this.charResponse = null;
    }

    async connect() {
        if (this.isConnected === true) {
            return;
        }

        let options = {};
        options.acceptAllDevices = true;
        options.optionalServices = [uuidServiceAdvertising, uuidServiceCommand];

        this.device = await navigator.bluetooth.requestDevice(options);

        if (this.device != null) {
            this.isConnected = true;
            this.device.addEventListener('gattserverdisconnected', this.onDisconnected);

            this.server = await this.device.gatt.connect();

            if (this.server != null) {
                // Get the 2 main services we use
                this.svcInfo = await this.server.getPrimaryService(uuidServiceAdvertising);
                this.svcCommand = await this.server.getPrimaryService(uuidServiceCommand);

                // Setup the listener for the command responses
                this.charResponse = await this.svcCommand.getCharacteristic(uuidCharResponse);
                this.charResponse.addEventListener('characteristicvaluechanged', this.onCharResponseValueChanged);
                this.charResponse.startNotifications();

                // Store the command characteristic as it will be frequently used
                this.charCommand = await this.svcCommand.getCharacteristic(uuidCharCommand);
            }
            else {
                this.isConnected = false;
            }
        }

        return this.isConnected;
    }

    disconnect() {
        if (this.device) {
            this.device.gatt.disconnect();
        }

        this.isConnected = false;
    }

    onDisconnected() {
        console.log('Disconnected');

        this.charResponse = null;
        this.charCommand = null;
        this.svcCommand = null;
        this.svcInfo = null;

        this.bleServer = null;
        this.bleDevice = null;
    }

    onCharResponseValueChanged(event) {
        //console.log('received response from pen...');
        //console.log(event.target.value.getUint8(0));

        switch (event.target.value.getUint8(0)) {
            case 5: // Temperature
                let currentTemperature = event.target.value.getUint8(1) + (event.target.value.getUint8(2) * 256);
                bleObserver.sendMsg(topics.BLE_CURRENT_SET_POINT, currentTemperature);
                break;
            /*
                        case 30: // Discreet Mode
                            let discreetMode = event.target.value.getUint8(1);
                            discreetModeToggle.checked = discreetMode === 1;
                            break;
            */
            case 140: // Battery Status
                let batteryPercentage = event.target.value.getUint8(1);
                bleObserver.sendMsg(topics.BLE_BATTERY_STATUS, batteryPercentage);
                break;

            case 173: // Puff Count
                let totalCount = event.target.value.getUint16(1, true);
                bleObserver.sendMsg(topics.BLE_PUFF_COUNT, totalCount);
                break;

            case 175: // Puff Time
                let totalTime = event.target.value.getFloat32(1, true);
                bleObserver.sendMsg(topics.BLE_PUFF_SECONDS, totalTime.toFixed(2));
                break;

            case 253:
                console.log(' BAD_COMMAND_SAFETY');
                break;

            case 254:
                console.log(' BAD_COMMAND_LENGTH');
                break;

            case 255:
                console.log(' BAD_REQUEST');
                break;

            default:
                console.log('UNKNOWN RESPONSE');
                break;
        }
    }

    getSetPointTemperature() {
        if (this.charCommand != null) {
            var cmdGetTemp = new ArrayBuffer(1);

            var data = new DataView(cmdGetTemp);
            data.setUint8(0, 5);

            return this.charCommand.writeValue(cmdGetTemp);
        }
    }

    setSetPointTemperature(temperature) {
        if (this.charCommand != null) {
            var cmdIncreaseTemp = new ArrayBuffer(3);

            var data = new DataView(cmdIncreaseTemp);
            data.setUint8(0, 4);
            data.setUint8(1, (temperature) % 256);
            data.setUint8(2, Math.floor((temperature) / 256));

            return this.charCommand.writeValue(cmdIncreaseTemp);
        }
    }

    getBatteryStatus() {
        if (this.charCommand != null) {
            // Force a refresh of the current battery level in the pen
            //var cmdForceBatteryUpdate = new ArrayBuffer(1);
            //var data = new DataView(cmdForceBatteryUpdate);
            //data.setUint8(0, 139);
            //await this.charCommand.writeValue(cmdForceBatteryUpdate);

            // Response will be retrieved from the response buffer
            var cmdGetBatteryUpdate = new ArrayBuffer(1);
            var data = new DataView(cmdGetBatteryUpdate);
            data.setUint8(0, 140);
            this.charCommand.writeValue(cmdGetBatteryUpdate);
        }
    }

    getPuffTime() {
        if (this.charCommand != null) {
            var cmdGetPuffTime = new ArrayBuffer(1);
            var data = new DataView(cmdGetPuffTime);
            data.setUint8(0, 175);

            this.charCommand.writeValue(cmdGetPuffTime);
        }
    }

    getPuffCount() {
        if (this.charCommand != null) {
            var cmdGetPuffCount = new ArrayBuffer(1);
            var data = new DataView(cmdGetPuffCount);
            data.setUint8(0, 173);

            this.charCommand.writeValue(cmdGetPuffCount);
        }
    }

    async getDeviceName() {
        if (this.svcInfo == null) {
            return '';
        }

        let decoder = new TextDecoder('utf-8');
        let charName = await this.svcInfo.getCharacteristic(uuidCharName);
        let deviceName = await charName.readValue();

        return decoder.decode(deviceName);
    }

    async getDeviceInfo() {
        if (this.svcInfo == null) {
            return null;
        }

        let charFirmware = await this.svcInfo.getCharacteristic(uuidCharFirmwareVersion);
        let firmware = await charFirmware.readValue();

        let decoder = new TextDecoder('utf-8');
        firmware = decoder.decode(firmware);
        console.log(`Firmware version: ${firmware}`);
    }

    pingDevice() {
        if (this.charCommand != null) {
            var cmdPingDevice = new ArrayBuffer(1);

            var data = new DataView(cmdPingDevice);
            data.setUint8(0, 28);

            return this.charCommand.writeValue(cmdPingDevice);
        }
    }
}

let bleController = new BLEController();

export default bleController;
