import React from 'react';
import { Redirect } from 'react-router-dom';
import bleController from '../controller/ble-controller';
import { route } from '../configurations';

import imgDownArrow from '../imgs/icons/icnDownArrow.png';
import imgLogoTweed from '../imgs/juju/logoTweed.png';
import imgPenOutline from '../imgs/juju/penOutline.png';


export default class HomeScreen extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            redirect: null
        };

        this.connectToDevice = this.connectToDevice.bind(this);
    }

    async connectToDevice() {
        try {
            let isConnected = await bleController.connect();

            if (isConnected === true) {
                this.setState({
                    redirect: route.JUJU_DASHBOARD
                });
            }
            else {
                console.log('Failed to connect');
            }
        }
        catch (e) {
            console.log('Error connecting to device');
            console.log(e);
        }
    }

    render() {
        if (this.state.redirect) {
            return <Redirect to={this.state.redirect} />
        }
        else {
            return (
                <div className="text-center" style={{ display: 'flex', flex: 1, flexDirection: 'column' }}>

                    <div className="container-fluid">
                        <div style={{ display: 'flex', flexDirection: 'row', marginTop: 5, marginBottom: 10 }}>
                            <div style={{ flex: 1, justifyContent: 'center', color: '#ffffff', fontSize: 24, fontFamily: 'Montserrat-Medium' }}>
                                DEVICE PAIRING
                            </div>
                        </div>
                    </div>


                    <div style={{ display: 'flex', flex: 1, flexDirection: 'column', marginLeft: 10, marginRight: 10, alignItems: 'stretch', borderRadius: 8, backgroundImage: `linear-gradient(#3c3e42, #4b4d52)` }}>

                        <div style={{ display: 'flex', flexDirection: 'row', marginTop: 10 }}>
                            <div style={{ justifyContent: 'center', width: '15%', alignSelf: 'center', }}>

                            </div>
                            <div style={{ flex: 1, justifyContent: 'center' }}>
                                <img className="text-center" src={imgLogoTweed} alt='' style={{ height: 70 }} />
                            </div>
                            <div style={{ width: '15%', justifyContent: 'center', alignSelf: 'center' }}>
                            </div>
                        </div>

                        <div style={{ flex: 1, display: 'flex', justifySelf: 'center', flexDirection: 'column', height: '100%', alignItems: 'stretch', marginTop: -10 }}>
                            <div style={{ color: '#ffffff', fontSize: 12, fontFamily: 'Montserrat-Medium', marginTop: 30 }}>
                                Press the JUJU fern button for 1 second to<br />make your device detectable
                            </div>
                            <div style={{ justifyContent: 'center', marginTop: 15 }}>
                                <img src={imgDownArrow} alt='' style={{ height: 50, width: 20 }} />
                            </div>

                        </div>

                        <div style={{ position: 'relative', paddingLeft: 0, paddingRight: 0 }}>
                            <img src={imgPenOutline} alt='' style={{ height: 220, width: 95, position: 'absolute', bottom: 0, left: '38%', zIndex: 0 }} />
                        </div>

                        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', paddingLeft: 0, paddingRight: 0, height: 80, backgroundColor: "#333333", color: '#ffffff', borderBottomLeftRadius: 8, borderBottomRightRadius: 8 }}>
                            <button className="btnConnect" style={{ backgroundColor: '#f4ce64' }} onClick={this.connectToDevice} >
                                CONNECT
                            </button>
                        </div>

                    </div>
                </div>
            );
        }
    }
}

