import React from 'react';
import { Redirect } from 'react-router-dom';
import bleController from '../../controller/ble-controller';
import { route } from '../../configurations';
import topics, { bleObserver } from '../../utilities/bleObserver';
import { CircleSlider } from 'react-circle-slider';

import imgUnlock from '../../imgs/icons/icnUnlock.png';
import imgDeviceSettings from '../../imgs/icons/icnDeviceSettings.png';
import imgLogoTweed from '../../imgs/juju/logoTweed.png';
import imgBannerPenelope from '../../imgs/juju/bannerPenelope.png';


export default class JujuDashboard extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            redirect: null,
            deviceName: 'unknown',
            setPointTemperature: 330,
            batteryPercentage: 0,
            puffCount: 0,
            puffSeconds: 0
        };

        this.subGetSetPoint = bleObserver.subscribe(topics.BLE_CURRENT_SET_POINT, this.onGetSetPointTemperature.bind(this));
        this.subGetBatteryStatus = bleObserver.subscribe(topics.BLE_BATTERY_STATUS, this.onGetBatterStatus.bind(this));
        this.subGetPuffCount = bleObserver.subscribe(topics.BLE_PUFF_COUNT, this.onGetPuffCount.bind(this));
        this.subGetPuffSeconds = bleObserver.subscribe(topics.BLE_PUFF_SECONDS, this.onGetPuffSeconds.bind(this));

        this.navigateToDeviceSettings = this.navigateToDeviceSettings.bind(this);
        this.disconnectFromDevice = this.disconnectFromDevice.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    async componentDidMount() {
        let deviceName = await bleController.getDeviceName();

        this.setState({
            deviceName: deviceName
        });

        setTimeout(() => { bleController.getSetPointTemperature(); }, 100);
        setTimeout(() => { bleController.getBatteryStatus(); }, 200);
        setTimeout(() => { bleController.getPuffCount(); }, 300);
        setTimeout(() => { bleController.getPuffTime(); }, 400);
    }

    componentWillUnmount() {
        this.subGetSetPoint.remove();
        this.subGetBatteryStatus.remove();
        this.subGetPuffCount.remove();
        this.subGetPuffSeconds.remove();
    }

    disconnectFromDevice() {
        bleController.disconnect();

        this.setState({
            redirect: route.HOME
        });
    }

    navigateToDeviceSettings() {
        this.setState({
            redirect: route.JUJU_SETTINGS
        });
    }

    handleChange = this.debounce(value => {
        bleController.setSetPointTemperature(value);

        this.setState({
            setPointTemperature: value
        });
    }, 15);

    onGetSetPointTemperature(temperature) {
        console.log(`GOT SETPOINT VALUE! ${temperature}`);

        this.setState({
            setPointTemperature: temperature
        });
    }

    onGetBatterStatus(batteryPercentage) {
        this.setState({
            batteryPercentage: batteryPercentage
        });
    }

    onGetPuffSeconds(puffSeconds) {
        this.setState({
            puffSeconds: puffSeconds
        });
    }

    onGetPuffCount(puffCount) {
        this.setState({
            puffCount: puffCount
        });
    }

    debounce(func, wait, immediate) {
        var timeout;
        return function () {
            var context = this, args = arguments;
            var later = function () {
                timeout = null;
                if (!immediate) func.apply(context, args);
            };
            var callNow = immediate && !timeout;
            clearTimeout(timeout);
            timeout = setTimeout(later, wait);
            if (callNow) func.apply(context, args);
        };
    };

    render() {
        if (this.state.redirect) {
            return <Redirect to={this.state.redirect} />
        }
        else {
            return (
                <div className="text-center" style={{ display: 'flex', flex: 1, flexDirection: 'column' }}>
                    <div style={{ display: 'flex', flex: 1, flexDirection: 'column', marginLeft: 10, marginRight: 10, alignItems: 'stretch', borderRadius: 8, backgroundImage: `linear-gradient(#3c3e42, #4b4d52)` }}>
                        <div style={{ display: 'flex', flexDirection: 'row', marginTop: 10 }}>
                            <div style={{ justifyContent: 'center', width: '15%', alignSelf: 'center', }}>
                                <img src={imgUnlock} alt='' style={{ height: 30, width: 30 }} />
                            </div>
                            <div style={{ flex: 1, justifyContent: 'center' }}>
                                <img className="text-center" src={imgLogoTweed} alt='' style={{ height: 70 }} />
                            </div>
                            <div style={{ width: '15%', justifyContent: 'center', alignSelf: 'center' }}>
                                <img src={imgDeviceSettings} alt='' style={{ height: 30, width: 30 }} onClick={this.navigateToDeviceSettings} />
                            </div>
                        </div>

                        <div style={{ flex: 1, display: 'flex', flexDirection: 'column', height: '100%', alignItems: 'stretch' }}>
                            <div className='noselect' style={{ justifyContent: 'center', color: '#f4ce64', marginTop: 25, fontFamily: 'Montserrat-Medium' }}>
                                PENELOPE
                            </div>
                            <div className='noselect' style={{ justifyContent: 'center', color: '#ffffff', fontSize: 12, fontFamily: 'Montserrat-Medium' }}>
                                {this.state.deviceName}
                            </div>
                            <div style={{ position: 'relative', flex: 1, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                <CircleSlider value={this.state.setPointTemperature} onChange={this.handleChange} min={300} max={400} progressWidth={4} knobRadius={15}
                                    circleColor={'#000000'} progressColor={'#f4ce64'} shadow={true} size={'275'} knobColor={'#f4ce64'}
                                />
                                <div className='noselect' style={{ pointerEvents: 'none', height: 300, width: 300, position: 'absolute', top: '36%', color: '#bbbbbb', fontSize: 16, fontFamily: 'Montserrat-Medium' }}>
                                    TEMPERATURE
                                </div>
                                <div className='noselect' style={{ pointerEvents: 'none', height: 300, width: 300, position: 'absolute', top: '41%', color: '#ffffff', fontSize: 48, fontFamily: 'Montserrat-Medium' }}>
                                    {this.state.setPointTemperature} °C
                                </div>
                            </div>
                        </div>

                        <div style={{ paddingLeft: 0, paddingRight: 0 }}>
                            <img src={imgBannerPenelope} alt='' style={{ height: 20, width: "100%" }} />
                        </div>

                        <div style={{
                            display: 'flex', justifyContent: 'center', alignItems: 'center', paddingLeft: 0,
                            paddingRight: 0, height: 80, backgroundColor: "#333333", color: '#ffffff', borderBottomLeftRadius: 8,
                            borderBottomRightRadius: 8, fontFamily: 'Montserrat-Medium'
                        }}>
                            Session Guard
                        </div>

                    </div>
                </div>
            );
        }
    }
}